import React from "react";
import '../../css/section2.css'
import plus from '../../material/plus.png';
import light from '../../material/light.png';
import audio from '../../material/audio.png';
import heat from '../../material/heat.png';
import sec from '../../material/sec.png';
import Shape from '../../material/Shape.png';
import Info from "./info/info";


const Section1 = () => {

  const [infLight , setInfLight] = React.useState(false)
  const [infHeat , setInfHeat] = React.useState(false)
  const [infAudio , setInfAudio] = React.useState(false)
  const [infSecurity , setInfSecurity] = React.useState(false)

  return (
    <div>
      <section className="section2 parallaxLayerBack">
        <div className="container">
          <h3>ПРЕИМУЩЕСТВА СИСТЕМЫ</h3>
          <div className="Shape">
            <div className="blockFirst">
              <div className="light">
                <a onMouseEnter={() => setInfLight(true)} onMouseLeave={() => setInfLight(false)}>
                  <img className="plus" src={plus} alt="" />
                {!infLight 
                ? <div className="ShapeText">
                    <p>Управляйте</p>
                    <p>освещением</p>
                  </div>
                  : <Info headerText={'Управляйте освещением'} contentText={'some text'}/>
                }
                  <img src={light} alt="" />
                </a>
             
              </div>
              <div className="audio">
                <a onMouseEnter={() => setInfAudio(true)} onMouseLeave={() => setInfAudio(false)}>
                  <img className="plus" src={plus} alt="" />
                  {!infAudio
                  ? <div className="ShapeText">
                    <p>Управляйте</p>
                    <p>мультимедиа</p>
                  </div>
                  : <Info headerText={'Управляйте мультимедиа'} contentText={'some text'}/>
                  }
                  <img src={audio} alt="" />
                </a>
              </div>
            </div>
            <img src={Shape} alt="" />
            <div className="blockSecond">
              <div className="climate">
                <a onMouseEnter={() => setInfHeat(true)} onMouseLeave={() => setInfHeat(false)}>
                  <img src={heat} alt="" />
                  {!infHeat
                  ? <div className="ShapeText">
                    <p>Управление</p>
                    <p>климатом</p>
                  </div>
                  : <Info headerText={'Управляйте климатом'} contentText={'some text'}/>
                  }
                  <img className="plus" src={plus} alt="" />
                </a>
              </div>
              <div className="security">
                <a onMouseEnter={() => setInfSecurity(true)} onMouseLeave={() => setInfSecurity(false)}>
                  <img src={sec} alt="" />
                  {!infSecurity
                  ? <div className="ShapeText">
                    <p>Управление</p>
                    <p>безопасностью</p>
                  </div>
                  : <Info headerText={'Управляйте безопасностью'} contentText={'some text'}/>
                  }
                  <img className="plus" src={plus} alt="" />
                </a>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Section1;
